<script>
  export default {
    name: "bill",
    components: {},
    data: function () {
      return {
        billNo: "",
        date: "",
        gstin: "",
        pono: "",
        to: "",
        items: [{
            HSNCode: "",
            Particulars: "",
            Quantity: 0,
            Rate: 0,
            Amount: ""
          },
          {
            HSNCode: "",
            Particulars: "",
            Quantity: 0,
            Rate: 0,
            Amount: ""
          },
          {
            HSNCode: "",
            Particulars: "",
            Quantity: 0,
            Rate: 0,
            Amount: ""
          },
          {
            HSNCode: "",
            Particulars: "",
            Quantity: 0,
            Rate: 0,
            Amount: ""
          },
          {
            HSNCode: "",
            Particulars: "",
            Quantity: 0,
            Rate: 0,
            Amount: ""
          }
        ],
        step: 1,
        backButtonVisible: true
      };
    },
    mounted() {
      let date = new Date();
      this.billNo = date.getFullYear() + "/";
    },
    methods: {
      addrow(count) {
        for (let i = 0; i < count; i++) {
          let item = {
            HSNCode: "",
            Particulars: "",
            Quantity: 0,
            Rate: 0,
            Amount: ""
          };

          this.items.push(item);
        }
      },
      deleterow(index) {
        this.items.splice(index, 1);
      },
      preview() {
        this.step = 2;
      },
      back() {
        this.step = 1;
      },
      print() {
        this.backButtonVisible = false;
        this.$nextTick().then(() => {
          window.print();
          this.backButtonVisible = true;
        });
      }
    },
    filters: {
      currency(value) {
        return value.toFixed(2);
      }
    },
    computed: {
      total() {
        return this.items.reduce((acc, item) => {
          return acc + item.Quantity * item.Rate;
        }, 0);
      }
    }
  };
</script>

